import React, { useState } from 'react';
import { WelcomeLayout } from 'shared/layouts/WelcomeLayout';
import { useFormik } from 'formik';
import * as yup from 'yup';
import { useDispatch } from 'react-redux';
import { KntInput, KntPhoneInput } from 'shared/components/Fields';
import Actions from 'store/actions';
import { useAppState } from 'shared/state';
import { RegisterInfo } from 'shared/state/reducer';
import { AxiosError } from 'axios';
import { Link } from 'react-router-dom';
import { Helmet } from 'react-helmet';
import { useTranslation } from 'react-i18next';
import Localization from '../../i18n';
const FormRequest = Actions.entity.formRequest;

const phoneRegExp =
	/^((\\+[1-9]{1,4}[ \\-]*)|(\\([0-9]{2,3}\\)[ \\-]*)|([0-9]{2,4})[ \\-]*)*?[0-9]{3,4}?[ \\-]*[0-9]{3,4}?$/;

const validationSchema = yup.object({
	username: yup
		.string()
		.email(Localization.t('valid_email'))
		.required(Localization.t('email_required')),
	first_name: yup.string().trim().required(Localization.t('firstname_required')),
	last_name: yup.string().trim().required(Localization.t('lastname_required')),
	mobile: yup
		.string()
		.required(Localization.t('mobile_required'))
		.matches(phoneRegExp, Localization.t('mobile_incorrect'))
		.min(10, Localization.t('too_short_validation')),
	password: yup
		.string()
		.strict(true)
		.trim(Localization.t('password_cannot_blank'))
		.min(6, Localization.t('password_validation'))
		.required(Localization.t('password_required')),
	passwordConfirmation: yup
		.string()
		.oneOf([yup.ref('password')], Localization.t('password_not_same'))
		.required(Localization.t('password_confirmation_required'))
});

function Signup() {
	const dispatch = useDispatch();
	const { t } = useTranslation();
	const { setRegisterInfo, language } = useAppState();
	const [existError, setExistError] = useState(false);
	const [isRegistered, setIsRegistered] = useState(false);
	const formik = useFormik({
		initialValues: {
			username: '',
			password: '',
			passwordConfirmation: '',
			first_name: '',
			last_name: '',
			mobile: ''
		},
		validationSchema: validationSchema,
		onSubmit: (values, { setFieldError, setSubmitting }) => {
			const filteredValues = {
				...values,
				...(language ? { language: language?.value } : {})
			};

			setExistError(false);
			// @ts-ignore
			delete filteredValues.passwordConfirmation;

			dispatch(
				FormRequest({
					entity: 'signup',
					name: 'signup',
					url: '/user/register-individual/',
					method: 'post',
					params: {},
					values: filteredValues,
					cb: {
						success: (data: any) => {
							setRegisterInfo(data as RegisterInfo);
							setSubmitting(false);
							setIsRegistered(true);
						},
						error: (error: AxiosError) => {
							if (error.response?.data?.code === 'username_exist_not_register') {
								dispatch(
									FormRequest({
										entity: 're-signup',
										name: 're-signup',
										url: '/user/re-register-individual/',
										method: 'patch',
										params: {},
										values: filteredValues,
										cb: {
											success: (data: any) => {
												if (process.env.NODE_ENV === 'development') {
													console.log('re-registered data', data);
												}

												setRegisterInfo(filteredValues);
												setIsRegistered(true);
											},
											error: (error: AxiosError) => {
												console.dir('error obj', error);
												if (error.response?.data?.code === 'username_exist') {
													setExistError(true);
												}
											},
											finally: () => {
												setSubmitting(false);
											}
										}
									})
								);
							}
							if (error.response?.data?.code === 'username_exist') {
								setExistError(true);
							}

							setSubmitting(false);
						},
						finally: () => {}
					}
				})
			);
		}
	});

	if (process.env.NODE_ENV === 'development') {
		console.log('formik values', formik.values);
	}

	return (
		<WelcomeLayout
			title={t('get_start_with_kennekt')}
			description={t('login_sub_info')}
			goToPageLink={'/signin'}
			goToPage={t('already_have_account')}
			goToPageLinkText={t('sign_in_lower')}
			back
			showLanguageSelect
			content={
				<>
					<Helmet>
						<title>KENNEKT | Sign up</title>
					</Helmet>

					<form onSubmit={formik.handleSubmit}>
						<div className=" w-full font-poppins md:w-[423px]">
							{isRegistered ? (
								<>
									<p className="whitespace-pre-wrap font-medium">
										{t('sent_confirm_link_to_email')}
									</p>
								</>
							) : (
								<>
									<KntInput
										name={'username'}
										placeholder={t('enter_email_address')}
										label={t('email_address')}
										required
										onChange={formik.handleChange}
										type={'email'}
										value={formik.values.username}
										error={formik.touched.username && Boolean(formik.errors.username)}
										helperText={formik.touched.username && formik.errors.username}
									/>
									<KntInput
										name={'first_name'}
										placeholder={t('enter_first_name')}
										label={t('first_name')}
										required
										onChange={formik.handleChange}
										value={formik.values.first_name}
										error={formik.touched.first_name && Boolean(formik.errors.first_name)}
										helperText={formik.touched.first_name && formik.errors.first_name}
									/>
									<KntInput
										name={'last_name'}
										placeholder={t('enter_last_name')}
										label={t('last_name')}
										required
										onChange={formik.handleChange}
										value={formik.values.last_name}
										error={formik.touched.last_name && Boolean(formik.errors.last_name)}
										helperText={formik.touched.last_name && formik.errors.last_name}
									/>
									<KntPhoneInput
										name={'mobile'}
										placeholder={t('enter_phone-number')}
										label={t('phone_number')}
										required
										setPhoneValue={formik.setFieldValue}
										value={formik.values.mobile}
										error={formik.touched.mobile && Boolean(formik.errors.mobile)}
										helperText={formik.touched.mobile && formik.errors.mobile}
										enableSearch
									/>
									<KntInput
										name={'password'}
										placeholder={t('password')}
										label={t('password')}
										required
										onChange={formik.handleChange}
										type={'password'}
										value={formik.values.password}
										error={formik.touched.password && Boolean(formik.errors.password)}
										helperText={formik.touched.password && formik.errors.password}
									/>
									<KntInput
										name={'passwordConfirmation'}
										placeholder={t('enter_confirm_password')}
										label={t('confirm_password')}
										required
										onChange={formik.handleChange}
										type={'password'}
										value={formik.values.passwordConfirmation}
										error={
											formik.touched.passwordConfirmation &&
											Boolean(formik.errors.passwordConfirmation)
										}
										helperText={
											formik.touched.passwordConfirmation && formik.errors.passwordConfirmation
										}
										classNames={'mb-8'}
									/>
									{existError ? (
										<p className={'mb-5 text-sm text-red-500'}>
											{t('user_already_exist_error')}{' '}
											<Link to={'/request-password'} className={'underline hover:no-underline'}>
												{t('recover_password')}
											</Link>
										</p>
									) : null}
									<button
										disabled={formik.isSubmitting}
										className={
											'bg-primary py-3.5 uppercase transition ' +
											'w-full rounded-lg text-center text-sm font-light text-white hover:bg-primary-dark'
										}
									>
										{formik.isSubmitting ? t('sign_up_please_wait') : t('sign_up')}
									</button>
									<p className={'mt-5 text-center text-xs font-light text-kgrey-welcome '}>
										{t('signin_label_info')}
									</p>
								</>
							)}
						</div>
					</form>
				</>
			}
		/>
	);
}

export default Signup;
